export default {
	data() {
		return {
			globalUser: {}
		};
	},
	onShow() {
		var me = this;
		var globalUser = me.getGlobalUser("globalUser");
		me.globalUser = globalUser;
	},
	methods: {
		modifySex() {
			uni.navigateTo({
				url: "../sex/sex"
			})
		},
		modifyBirthday() {
			uni.navigateTo({
				url: "../meBirthday/meBirthday"
			})
		},
		modifyNickname() {
			uni.navigateTo({
				url: "../meNickname/meNickname"
			})
		},
		operator() {
			var me = this;
			var globalUser = me.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册选择上传"],
				success(res) {
					var index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success(res) {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];






								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath + '&qq=1105254074'
								})

							}
						})
						
					}
				}
			})
		},
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			})
		},
		logout() {
			var globalUser = this.getGlobalUser("globalUser");
			var serverUrl = this.serverUrl;
			uni.request({
				url: serverUrl + '/user/logout?userId=' + globalUser.id,
				method: "POST",
				success: (res) => {
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						// 如果服务器返回200，代表用户在服务端退出登录成功
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url: "../me/me"
						})
					}
				}
			});
		}
	}
}
